import { Component, OnInit } from '@angular/core';

import { TorusService } from '../../services/torus/torus.service';
import { ImportService } from '../../services/import/import.service';
import { MessageService } from '../../services/message/message.service';
import { Router } from '@angular/router';
import { WalletService } from '../../services/wallet/wallet.service';

@Component({
  selector: 'app-torus',
  templateUrl: './torus.component.html',
  styleUrls: ['./torus.component.scss']
})
export class TorusComponent implements OnInit {

  constructor(
    private walletService: WalletService,
    private torusService: TorusService,
    private importService: ImportService,
    private messageService: MessageService,
    private router: Router
  ) { }

  ngOnInit(): void {
  }
  async torusLogin() {
    this.messageService.startSpinner('Alohomora ');
    const { keyPair, userInfo } = await this.torusService.loginTorus(undefined).catch(async (e) =>
      await this.messageService.stopSpinner()
    );
    console.log('login done');
    if (keyPair) {
      console.log(keyPair);
      await this.importService
        .importWalletFromPk(keyPair.pk, '', { verifier: userInfo.typeOfLogin, id: userInfo.verifierId, idToken: userInfo.idToken, accessToken: userInfo.accessToken})
        .then((success: boolean) => {
          if (success) {
            console.log('success');
            if (this.walletService.wallet.implicitAccounts.length === 1 && this.walletService.wallet.implicitAccounts[0].originatedAccounts.length === 0) {
              console.log('single address');
              this.router.navigate([`/account/${this.walletService.wallet.implicitAccounts[0].address}`]);
            } else {
              this.router.navigate(['/accounts']);
            }
            this.messageService.stopSpinner();
          } else {
            console.log(success);
            this.messageService.addError('Torus import failed');
            this.messageService.stopSpinner();
          }
        });
    } else {
      await this.messageService.stopSpinner();
    }
  }
}
